<!-- eslint-disable -->
<template>
  <div class="slideshow-wrapper" :style="{'background-image': 'url(' + img + ')'}">
    <div id="slideshow">
      <div class="slide">
        <div class="copy">
          <div class="title">Perguntas Frequentes</div>
          <!-- <div class="line"></div>
          <div class="text">
            Lorem ipsum dolor sit amet, consectetur adipisicing elit,
            sed do eiusmod tempor incididunt ut labore et dolore
            magna aliqua. Ut enim ad minim
          </div> -->
        </div>
      </div>
    </div>
  </div>
</template>

<script>
/* eslint-disable */
import img from "../../assets/cadeira.png";

export default {
  data() {
    return {
      img: img
    };
  }
};
</script>

<style lang="scss" scoped>
//colors
$white: #fff;
$black: #000;
$dk-gray-1: #393939;
$dk-gray-2: #464646;
$dk-gray-3: #707070;
$lt-gray: #f6f6f6;
$turquiose: rgba(11, 171, 197, .6);

@function pxToEm($value) {
  $point: ($value * 3) / 4;
  $yem: $point / 12;

  @return $yem * 1rem;
}

//paddings and margins
$pd-desktop: 0 pxToEm(390);
$mg-desktop: 0 pxToEm(40);

//circle
$circle: 50%;
.slideshow-wrapper {
  background-color: $white;
  height: pxToEm(383.5);
  padding: $pd-desktop;
  background-position: bottom left;
  background-repeat: no-repeat;
  background-size: 30%;

  @media only screen and (min-width: 1200px) and (max-width: 1919px) {
    padding: 0 pxToEm(80);
    background-size: contain;
  }

  @media only screen and (min-width: 992px) and (max-width: 1199px) {
    padding: 0 pxToEm(60);
    background-size: contain;
  }

  @media only screen and (min-width: 768px) and (max-width: 991px) {
    padding: 0 pxToEm(40);
    background-size: contain;
    background-position: top center;
  }

  @media only screen and (min-width: 600px) and (max-width: 767px) {
    padding: 0 pxToEm(20);
    background-size: contain;
    background-position: top center;
  }

  @media only screen and (min-width: 300px) and (max-width: 599px) {
    // height: auto;
    padding: 0 pxToEm(20);
    background-size: cover;
    background-position: bottom center;
  }

  #slideshow {
    height: 100%;
    margin: $mg-desktop;

    .slide {
      width: 100%;
      height: 100%;
      display: flex;
      align-items: center;
      position: relative;
      justify-content: flex-end;

      @media only screen and (max-width: 991px) {
        justify-content: center;
        align-items: flex-start;
      }

      .copy {
        position: relative;
        display: flex;
        flex-direction: column;
        width: 100%;
        max-width: pxToEm(900);
        padding: pxToEm(40);
        justify-content: space-between;
        align-items: flex-end;

        @media only screen and (max-width: 991px) {
          align-items: center;
          text-align: center;
          top: pxToEm(20);
          padding: 0;
          max-width: 100%;
          background-color: rgba(255,255,255,0.6);
        }

        .title {
          color: $turquiose;
          font-size: pxToEm(40);
          text-align: right;
          font-family: 'Dalmatins';

          @media only screen and (max-width: 991px) {
            font-size: pxToEm(28);
          }

          @media only screen and (max-width: 599px) {
            text-align: center;
          }
        }

        // .line {
        //   height: pxToEm(1);
        //   width: pxToEm(220);
        //   background-color: $dk-gray-3;
        //   margin-top: pxToEm(-50);
        // }

        // .text {
        //   color: $dk-gray-3;
        //   font-size: pxToEm(20);
        //   font-family: Arial, Helvetica, sans-serif;
        //   line-height: pxToEm(30);
        //   text-align: right;
        // }
      }
    }
  }
}
</style>



